"use strict";
export function formatTokenMetatagTitleName(symbol, name) {
  if (symbol) {
    return "Get " + symbol + " on Uniswap";
  }
  if (name) {
    return "Get " + name + " on Uniswap";
  }
  return "View Token on Uniswap";
}
export function formatNFTAssetMetatagTitleName(name, collectionName, tokenId) {
  if (name) {
    return name;
  }
  if (collectionName && tokenId) {
    return collectionName + " #" + tokenId;
  }
  if (tokenId) {
    return "Asset #" + tokenId;
  }
  return "View NFT on Uniswap";
}
